* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::before {
  box-sizing: border-box;
}

*::after {
  box-sizing: border-box;
}

body {
  height: 100vh;
  min-width: 100vw;
  background: #f4cf0f;
}

.dog {
  width: 120px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.body {
  height: 300px;
  background: #f79e38;
  border-radius: 60px 60px 0 0;
}

.ears {
  display: flex;
  justify-content: space-between;
  position: absolute;
  width: 100%;
  z-index: -1;
  top: -20px;
}

.ears::before,
.ears::after {
  background: #fff;
  content: "";
  width: 40px;
  height: 90px;
  border-radius: 100% 100% 0 0;
  border: 10px solid #f79e38;
}

.eyes {
  display: block;
  position: relative;
  width: 20px;
  height: 30px;
  background: #fff;
  border-radius: 10px 10px 0 0;
  margin: 0 auto;
  top: 2px;
}

.eyes::before,
.eyes::after {
  position: absolute;
  bottom: 2px;
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background: #000;
}

.eyes::before {
  left: -13px;
}

.eyes::after {
  right: -13px;
}

.beard {
  position: relative;
  width: 50px;
  height: 60px;
  margin: 0 auto;
  background: #ffffff;
  border-radius: 20px 20px 20px 20px;
}

.beard::before {
  position: absolute;
  width: 20px;
  height: 14px;
  border-radius: 0 0 15px 15px;
  background: black;
}

.mouth {
  position: absolute;
  width: 30px;
  height: 14px;
  border-radius: 0 0 15px 15px;
  background: #000000;
  top: 20px;
  left: 0;
  right: 0;
  margin: auto;
}

.beard::before {
  position: absolute;
  width: 30px;
  height: 14px;
  border-radius: 0 0 15px 15px;
  background: #000000;
}
